.header {
    background-color: rgb(141, 88, 141);
    height: 6rem;
    color: #fff;
}

h2 {
    line-height: 6rem;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

#logo-title {
    cursor: pointer;
}

#logo-title:hover {
    color: #ff0000;
}



